import React, { Fragment, useEffect, useState } from 'react'
import axios from 'axios'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import { Link, useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { divIcon } from 'leaflet'

const Settings = () => {
    const [pointRuleloading, setPointRuleloading] = useState(false)
    const [userInput, setUserInput] = useState({
            app_id: '',
            app_secret: '',
            user_token: '',
            pageId: '',
            adAccountId: ''
    })
    const handleChange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
    useEffect(() => {
        useJwt.getCampaignSettings().then(res => {
            console.log(res)
            setUserInput({
                    app_id: res.data.payload.app_id,
                    app_secret: res.data.payload.app_secret,
                    user_token: res.data.payload.user_token,
                    pageId: res.data.payload.page_id,
                    adAccountId: res.data.payload.ad_account_id
            })
        }).catch(err => {
            Error(err)
            console.log(err)
        })
    }, [])
    const onSubmit = (e) => {
        e.preventDefault()
        const  facebook  = userInput
        setPointRuleloading(true)
        useJwt.updateCampaignSettings({ facebook }).then((response) => {
            setPointRuleloading(false)
            Success(response)
          }).catch((error) => {
            setPointRuleloading(false)
            Error(error)
            console.log(error)
          })
    }
    return (
        <Fragment>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Facebook</CardTitle>
                </CardHeader>
                <CardBody style={{ paddingTop: '15px' }}>
                    <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="app_id">App ID</Label>
                            <Input type="text"
                                name="app_id"
                                id='app_id'
                                value={userInput.app_id}
                                onChange={handleChange}
                                required
                                placeholder="app id"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="app_secret">App Secret</Label>
                            <Input type="text"
                                name="app_secret"
                                id='app_secret'
                                value={userInput.app_secret}
                                onChange={handleChange}
                                required
                                placeholder="app secret"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="pageId">Page ID</Label>
                            <Input type="text"
                                name="pageId"
                                id='pageId'
                                value={userInput.pageId}
                                onChange={handleChange}
                                required
                                placeholder="Page id"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="adAccountId">Ad Account ID</Label>
                            <Input type="text"
                                name="adAccountId"
                                id='adAccountId'
                                value={userInput.adAccountId}
                                onChange={handleChange}
                                required
                                placeholder="ad account id"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="user_token">User Token</Label>
                            <Input type="textarea"
                                rows = "6"
                                name="user_token"
                                id='user_token'
                                value={userInput.user_token}
                                onChange={handleChange}
                                required
                                placeholder="user token"
                            />
                        </FormGroup>
                    </Col>
                        <Col sm="12" className='text-center'>
                            {
                                pointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                    <Spinner color='white' size='sm' />
                                    <span className='ml-50'>Loading...</span>
                                </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                    <span >Update</span>
                                </Button.Ripple>
                            }
                        </Col>
                    </Form>
                </CardBody>
            </Card>
            <Card> 
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Coming soon...</CardTitle>
                </CardHeader>
            </Card>
        </Fragment>
    )
}

export default Settings